import {
	mapState
} from 'vuex'
export default {
	computed: mapState(['version']),
	data() {
		return {

		}
	},
	methods: {
		copy: function(text) {
			const that = this
			uni.setClipboardData({
				data: text,
				success(res) {
					uni.getClipboardData({
						success(res) {
							//that.tui.toast("邮箱已复制", 2000, true)
						}
					})
				}
			})
		},
		log: function() {
			uni.navigateTo({
				url: '../log/log'
			})
		}
	}
}
